.view-link {
  padding-right: 12px;
  column-gap: 8px;
  align-content: center;
  grid-template-columns: max-content 1fr;
  grid-auto-flow: column;
  display: inline-grid;
  outline: none;
  transition: .25s ease-out;
  text-decoration: none;
  border-radius: 8px;
  border: 1px solid #E1EBFA;
  height: 56px;

  &:focus-within {
    box-shadow: 0 0 0 4px rgba(#E1EBFA, .45);
  }
}

.view-link__emoji {
  justify-content: center;
  align-content: center;
  display: grid;
  font-size: 24px;
  height: 100%;
  width: 56px;
}

.view-link__title {
  line-height: 24px;
  font-weight: 400;
  font-size: 15px;
  color: #000B19;
}