@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap);
.menu{grid-row-gap:24px;row-gap:24px;display:grid}

.page-header{border-bottom:1px solid #E1EBFA;box-sizing:border-box;padding:0 26px}.page-header__page-back{justify-content:center;align-items:center;display:grid;padding:6px;border:none;outline:none;cursor:pointer;background-color:transparent}.page-header__content{padding:14px 0;grid-column-gap:4px;-webkit-column-gap:4px;column-gap:4px;align-items:center;grid-template-columns:32px 1fr 32px;grid-auto-flow:column;display:grid}.page-header__title{text-align:center;letter-spacing:-0.4px;font-weight:700;font-size:17px}.page-header__footer{padding-bottom:8px}

.page-tab-provider{grid-column-gap:25px;-webkit-column-gap:25px;column-gap:25px;display:flex}.page-tab-provider__tab{width:100%;position:relative;box-sizing:border-box;padding:18px 6px 8px 6px;border:none;outline:none;cursor:pointer;background-color:transparent;white-space:nowrap;line-height:22px;font-family:inherit;font-size:inherit;font-weight:500;color:#7B8392}.page-tab-provider__tab:after{content:"";left:0;bottom:0;right:0;position:absolute;border-radius:30px;background-color:transparent;display:inline-block;height:2px;width:100%}.page-tab-provider__tab_current{color:#000B19}.page-tab-provider__tab_current:after{background-color:#006AFF}

.page{min-height:100vh;grid-template-rows:-webkit-min-content -webkit-max-content;grid-template-rows:min-content max-content;grid-row-gap:24px;row-gap:24px;display:grid;background-color:#fff}.page__content{display:flex;overflow-x:hidden}.page__slide{flex-shrink:0;width:100%}

.container{box-sizing:border-box;padding:0 16px}

.avatar{overflow:hidden}.avatar_size_small{border-radius:10px;height:32px;width:32px}.avatar_size_medium{border-radius:16px;height:56px;width:56px}.avatar_size_large{border-radius:24px;height:96px;width:96px}.avatar__image{object-fit:cover;height:100%;width:100%}

.header{box-sizing:border-box;padding:24px 12px 24px 24px;align-items:center;justify-content:space-between;grid-auto-flow:column;display:grid}.header__brand{grid-column-gap:34px;-webkit-column-gap:34px;column-gap:34px;align-items:center;grid-auto-flow:column;display:inline-grid}.header__logo{justify-content:center;align-items:center;display:grid;position:relative}.header__logo:after{content:"";margin:auto auto auto 2px;top:0;right:0;bottom:0;left:100%;position:absolute;transform:rotate(130deg);background-color:#EAEAEA;display:inline-block;height:1px;width:24px}.header__user{grid-column-gap:12px;-webkit-column-gap:12px;column-gap:12px;align-items:center;grid-auto-flow:column;display:inline-grid}.header__username{line-height:100%;font-weight:500}.header__settings{padding:12px;justify-content:center;align-items:center;display:grid}

.upload-image{box-sizing:border-box;padding:0 16px 32px 16px;grid-column-gap:16px;-webkit-column-gap:16px;column-gap:16px;align-items:center;grid-template-columns:-webkit-min-content -webkit-max-content;grid-template-columns:min-content max-content;grid-auto-flow:column;display:grid}.upload-image__button{text-align:left;font-weight:500;font-family:inherit;font-size:inherit;color:#006AFF;background-color:transparent;cursor:pointer;outline:none;border:none;padding:0}

.input{margin-bottom:6px;position:relative;display:grid}.input__field{outline:none;border-radius:8px;background-color:#fff;border:1px solid #E1EBFA;box-sizing:border-box;padding:16px;line-height:20px;font-family:inherit;-webkit-appearance:none;font-size:inherit}.input__label{transform:translateX(16px) translateY(16px);position:absolute;color:#7B8392;background-color:#fff;transition:.25s ease}.input__label_collapsed{transform:translateX(10px) translateY(-6px);padding:0 9px;letter-spacing:0.03em;line-height:16px;font-weight:700;font-size:12px}

.grid{grid-row-gap:8px;row-gap:8px;display:grid}

.button{display:inline-grid;box-sizing:border-box;padding:12px 20px;line-height:24px;font-weight:700;font-size:17px;color:#fff;transition:.25s ease-out;outline:none;background-color:#006AFF;border-radius:8px}.button:focus-within{box-shadow:0 0 0 4px rgba(0,106,255,0.25)}

.menu-button{position:relative;box-sizing:border-box;padding:14px 32px;outline:none;transition:.25s ease-out;background-color:transparent;text-decoration:none;color:inherit;grid-column-gap:20px;-webkit-column-gap:20px;column-gap:20px;align-items:center;grid-template-columns:-webkit-min-content 1fr;grid-template-columns:min-content 1fr;grid-auto-flow:column;display:grid;width:100%}.menu-button:after{content:"";display:inline-block;background-image:url("data:image/svg+xml,%3Csvg width='12' height='12' viewBox='0 0 12 12' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg clip-path='url(%23clip0)'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M2.79286 0.292893C2.40234 0.683417 2.40234 1.31658 2.79286 1.70711L7.08576 6L2.79286 10.2929C2.40234 10.6834 2.40234 11.3166 2.79286 11.7071C3.18339 12.0976 3.81655 12.0976 4.20708 11.7071L8.49997 7.41421C9.28102 6.63316 9.28102 5.36683 8.49997 4.58579L4.20708 0.292893C3.81655 -0.0976311 3.18339 -0.0976311 2.79286 0.292893Z' fill='%23C1CADA'/%3E%3C/g%3E%3Cdefs%3E%3CclipPath id='clip0'%3E%3Crect width='12' height='12' fill='white'/%3E%3C/clipPath%3E%3C/defs%3E%3C/svg%3E%0A");height:12px;width:12px}.menu-button_disabled .menu-button__icon svg{fill:#C1CADA}.menu-button_disabled .menu-button__title{color:#C1CADA}.menu-button_disabled:after{display:none}.menu-button:not(.menu-button_disabled):hover,.menu-button:not(.menu-button_disabled):focus-visible{background-color:#F2F7FE}.menu-button:not(.menu-button_disabled):active{background-color:#E1EBFA}.menu-button__icon{height:24px;width:24px}.menu-button__icon svg{fill:#000B19}.menu-button__title{line-height:24px;font-weight:500}

.menu-group__title{padding:16px 32px;letter-spacing:0.02em;line-height:16px;font-weight:600;font-size:12px;color:#7B8392}

.view-button{margin:8px;outline:none;border-radius:20px;transition:.25s ease-out;justify-content:center;align-content:center;display:grid;height:72px;width:72px}.view-button_vk{background:radial-gradient(100% 100% at 50% 0%, rgba(255,255,255,0.2) 0%, rgba(255,255,255,0) 100%),#1E7BE5;box-shadow:0 6px 12px rgba(30,123,229,0.15)}.view-button_vk:focus-visible{box-shadow:0 0 0 6px rgba(30,123,229,0.25)}.view-button_telegram{background:radial-gradient(100% 100% at 50% 0%, rgba(255,255,255,0.2) 0%, rgba(255,255,255,0) 100%),#2CA5E0;box-shadow:0 6px 12px rgba(44,165,224,0.15)}.view-button_telegram:focus-visible{box-shadow:0 0 0 6px rgba(44,165,224,0.25)}.view-button_behance{background:radial-gradient(100% 100% at 50% 0%, rgba(255,255,255,0.2) 0%, rgba(255,255,255,0) 100%),#1769FF;box-shadow:0 6px 12px rgba(23,105,255,0.15)}.view-button_behance:focus-visible{box-shadow:0 0 0 6px rgba(23,105,255,0.25)}

.view-link{padding-right:12px;grid-column-gap:8px;-webkit-column-gap:8px;column-gap:8px;align-content:center;grid-template-columns:-webkit-max-content 1fr;grid-template-columns:max-content 1fr;grid-auto-flow:column;display:inline-grid;outline:none;transition:.25s ease-out;text-decoration:none;border-radius:8px;border:1px solid #E1EBFA;height:56px}.view-link:focus-within{box-shadow:0 0 0 4px rgba(225,235,250,0.45)}.view-link__emoji{justify-content:center;align-content:center;display:grid;font-size:24px;height:100%;width:56px}.view-link__title{line-height:24px;font-weight:400;font-size:15px;color:#000B19}

.view-page{box-sizing:border-box;padding-top:100px;align-items:flex-end;display:grid;min-height:100vh;height:100%;width:100%}@media screen and (min-width: calc(768px +  1px)){.view-page{align-items:center;padding:24px 0;box-sizing:border-box}}.view-page__container{margin-left:auto;margin-right:auto;max-width:375px;position:relative;padding-top:calc(48px)}.view-page__box{height:100%;box-shadow:0 0 125px 4px rgba(0,0,0,0.1);align-content:flex-start;display:grid;box-sizing:border-box;padding:64px 32px 150px 32px;background-color:#fff;border-top-left-radius:12px;border-top-right-radius:12px}@media screen and (min-width: calc(768px +  1px)){.view-page__box{border-radius:12px;padding-bottom:100px}}.view-page__avatar{position:absolute;top:0;left:0;right:0;margin:auto;border:4px solid #fff;border-radius:24px;overflow:hidden;height:96px;width:96px}.view-page__avatar .avatar{border-radius:0;transform:scale(1.1)}.view-page__content{grid-row-gap:36px;row-gap:36px;display:grid}.view-page__information{text-align:center;grid-row-gap:8px;row-gap:8px;display:grid}.view-page__username{line-height:28px;font-weight:700;font-size:22px}.view-page__description{line-height:22px;color:#424A57}.view-page__buttons{flex-wrap:wrap;justify-content:center;grid-auto-flow:column;display:flex}.view-page__links{grid-row-gap:8px;row-gap:8px;display:grid}.view-page__copyright{text-align:center;letter-spacing:-0.05px;line-height:18px;font-size:13px;color:#424A57}.view-page__button{left:16px;right:16px;bottom:24px;position:absolute;text-align:center;display:grid}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}a{text-decoration:none;color:#006AFF}a.link{transition:.25s ease-out;outline:none}a.link:focus-within{text-decoration:underline}body{line-height:24px;font-weight:normal;font-family:'Inter', sans-serif;font-size:15px;color:#000B19}.js-focus-visible :focus:not(.focus-visible){outline:none}.page_layout_profile{padding-bottom:72px;box-sizing:border-box}.page_layout_profile .button{text-align:center;position:fixed;right:16px;left:16px;bottom:24px}

